@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(lottery) {
  overflow: hidden;
  background-color: $--background-color-page;

  @include e(banner) {
    height: 300px;
  }

  @include e(header) {
    position: relative;
    width: $--content-width;
    height: 34px;
    margin: 40px auto 0;
    font-size: 16px;
    line-height: 34px;
    color: #fff;
    border-bottom: 1px solid #323232;

    &::before {
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 16px * 4;
      height: 2px;
      content: '\20';
      border-bottom: 2px solid #cc2944;
    }
  }

  @include e(content) {
    width: $--content-width;
    margin: 20px auto 60px;
  }

  @include e(item) {
    position: relative;
    float: left;
    width: 587px;
    height: 310px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    transition: all 0.2s ease;

    & + & {
      margin-left: $--content-width - 587 * 2;
    }

    &:hover {
      transform: translateY(-2px);

      @include e(info) {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  @include e(title) {
    position: absolute;
    top: 20px;
    left: 26px;
    font-size: 22px;
    color: #ccc;
  }

  @include e(info) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: center;
    visibility: hidden;
    background: rgba(0, 0, 0, 0.2) radial-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.4));
    opacity: 0;
    transition: all 0.2s ease;
  }

  @include e(name) {
    display: inline-block;
    height: 40px;
    margin-top: 75px;
    font-size: 22px;
    color: #ccc;
    vertical-align: top;
    border-bottom: 2px solid #cc2944;
  }

  @include e(button) {
    margin-top: 20px;

    @include button(115px, 37px);
    @include button-solid();
  }

  @include e(describe) {
    margin-top: 20px;
    font-size: 13px;
    line-height: 24px;
    color: #cecece;

    & + & {
      margin-top: 0;
    }
  }
}
